--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
---------------------------------------------------------------*/
a {
  color: #343533;
  text-decoration: underline;
}
a:visited {
  color: #343533;
}
a:hover, a:focus, a:active {
  color: #222222;
  text-decoration: none;
}
a:focus {
  outline: thin dotted;
}
a:hover, a:active {
  outline: 0;
}
a.btn-link {
  border-bottom: none;
  box-shadow: none;
  background-color: transparent;
}
a.btn-link:hover {
  background-color: transparent;
}

/* Reset admin bar links style in admin bar */
#wpadminbar a {
  text-decoration: none;
  border-bottom: 0;
  background-color: transparent;
  box-shadow: none;
}

/* Remove links style */
a.woocommerce-product-gallery__trigger,
.woocommerce-product-gallery__image > a {
  text-decoration: none;
  border-bottom: 0;
  background-color: transparent;
  box-shadow: none;
}

/*--------------------------------------------------------------
## Menus
---------------------------------------------------------------*/
.main-navigation {
  display: block;
  width: unset;
  justify-content: center;
}
@media screen and (min-width: 1024px) {
  .main-navigation {
    display: flex;
    justify-content: flex-end;
  }
}
.main-navigation .mobile-nav-overlay {
  content: "";
  display: block;
  position: fixed;
  background: rgba(0, 0, 0, 0.7);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 500ms;
  z-index: -1;
}
.main-navigation #primary-menu-wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  overflow: hidden;
  z-index: 99;
  transition: all 0.3s ease-in-out;
}
@media screen and (min-width: 1024px) {
  .main-navigation #primary-menu-wrap {
    position: relative;
    width: unset;
    height: auto;
    overflow: unset;
    min-height: 1px !important;
  }
}
.main-navigation #primary-menu-wrap #primary-menu {
  position: relative;
  height: 100%;
  margin: 0;
  padding: 3em 0 0 0;
  list-style: none;
}
@media screen and (min-width: 1024px) {
  .main-navigation #primary-menu-wrap #primary-menu {
    display: block;
    margin: 0;
    padding: 0;
    height: auto;
  }
}
.main-navigation #primary-menu-wrap #primary-menu .content-width {
  display: flex;
  flex-direction: column;
  justify-content: unset;
  align-items: unset;
  padding: 0;
}
@media screen and (min-width: 1024px) {
  .main-navigation #primary-menu-wrap #primary-menu .content-width {
    flex-direction: row;
  }
}
.main-navigation #primary-menu-wrap #primary-menu ul {
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
  float: left;
  position: absolute;
  top: 1.5em;
  left: -999em;
  z-index: 99999;
}
.main-navigation #primary-menu-wrap #primary-menu ul ul {
  left: -999em;
  top: 0;
}
.main-navigation #primary-menu-wrap #primary-menu ul li:hover > ul, .main-navigation #primary-menu-wrap #primary-menu ul li.focus > ul {
  left: 100%;
}
.main-navigation #primary-menu-wrap #primary-menu ul a {
  width: 200px;
}
.main-navigation #primary-menu-wrap #primary-menu #menu-close {
  display: block;
  position: absolute;
  top: 5px;
  right: 10px;
  color: #333;
  padding: 1em;
  z-index: 999;
  cursor: pointer;
}
@media screen and (min-width: 1024px) {
  .main-navigation #primary-menu-wrap #primary-menu #menu-close {
    display: none;
  }
}
.main-navigation #primary-menu-wrap #primary-menu li:hover > ul,
.main-navigation #primary-menu-wrap #primary-menu li.focus > ul {
  left: auto;
}
.main-navigation #primary-menu-wrap li {
  position: relative;
  border-bottom: 1px solid #e2e2e2;
  transform: translate3d(-150px, 0, 0);
  transition: transform 0.15s ease-in-out;
}
@media screen and (min-width: 1024px) {
  .main-navigation #primary-menu-wrap li {
    border-bottom: 0;
    transform: translate3d(0, 0, 0);
  }
}
.main-navigation #primary-menu-wrap a {
  display: block;
  text-decoration: none;
  padding: 1.5em 1em;
  text-align: center;
}
@media screen and (min-width: 1024px) {
  .main-navigation #primary-menu-wrap .menu-social-menu-container {
    display: none;
  }
}
.main-navigation #primary-menu-wrap .menu-social-menu-container #social-menu {
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-navigation #primary-menu-wrap .menu-social-menu-container #social-menu .content-width {
  display: flex;
}
.main-navigation #primary-menu-wrap .menu-social-menu-container #social-menu .content-width li {
  display: flex;
  justify-content: center;
  width: 100%;
  transform: translate3d(0, 0, 0);
  border-bottom: none;
}
.main-navigation #primary-menu-wrap .menu-social-menu-container #social-menu .content-width li a {
  flex: 1 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main-navigation #primary-menu-wrap .menu-social-menu-container #social-menu .content-width li a svg {
  width: 20px;
  height: 20px;
}
.main-navigation #primary-menu-wrap .menu-social-menu-container #social-menu .content-width li a:hover {
  color: #222222;
}
.main-navigation #primary-menu-wrap .menu-social-menu-container #social-menu .content-width li a[title=instagram]:hover {
  background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
}
.main-navigation #primary-menu-wrap .menu-social-menu-container #social-menu .content-width li a[title=twitter]:hover {
  background: #00aced;
}
.main-navigation #primary-menu-wrap .menu-social-menu-container #social-menu .content-width li a[title=pinterest]:hover {
  background: #E60023;
}
.main-navigation #menu-toggle {
  display: inline-block;
  transition: all 0.3s ease-out;
  cursor: pointer;
  padding: 1em;
}
@media screen and (min-width: 1024px) {
  .main-navigation #menu-toggle {
    display: none;
  }
}
.main-navigation #menu-toggle .menu-icon {
  height: 20px;
  width: 20px;
  position: relative;
  z-index: 2;
  cursor: pointer;
  display: block;
}
.main-navigation #menu-toggle .menu-icon .menu-icon__line {
  height: 2px;
  width: 20px;
  display: block;
  background-color: #000;
  margin-bottom: 5px;
  cursor: pointer;
  transition: background-color 0.5s ease, -webkit-transform 0.2s ease;
  transition: transform 0.2s ease, background-color 0.5s ease;
  transition: transform 0.2s ease, background-color 0.5s ease, -webkit-transform 0.2s ease;
}
.main-navigation #menu-toggle .menu-icon .menu-icon__line-left {
  width: 11px;
  -webkit-transition: all 200ms linear;
  transition: all 200ms linear;
}
.main-navigation #menu-toggle .menu-icon .menu-icon__line-right {
  width: 11px;
  float: right;
  transition: all 200ms linear;
}
.main-navigation #menu-toggle .menu-icon:hover .menu-icon__line-left,
.main-navigation #menu-toggle .menu-icon:hover .menu-icon__line-right {
  width: 20px;
}

.admin-bar #primary-menu-wrap {
  top: 46px;
}
@media screen and (min-width: 782px) {
  .admin-bar #primary-menu-wrap {
    top: 32px;
  }
}
@media screen and (min-width: 1024px) {
  .admin-bar #primary-menu-wrap {
    top: 0;
  }
}

.menu--active {
  height: 100vh;
  overflow: hidden;
}
@media screen and (min-width: 1024px) {
  .menu--active {
    overflow: unset;
  }
}
.menu--active .main-navigation .mobile-nav-overlay {
  opacity: 1;
  transition: opacity 500ms;
  z-index: 9;
}
@media screen and (min-width: 1024px) {
  .menu--active .main-navigation .mobile-nav-overlay {
    display: none;
    position: relative;
    width: 1px;
    height: 1px;
    opacity: 0;
    z-index: -1;
  }
}
.menu--active .main-navigation #primary-menu-wrap {
  width: 60%;
  position: fixed;
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .menu--active .main-navigation #primary-menu-wrap {
    width: 40%;
  }
}
@media screen and (min-width: 1024px) {
  .menu--active .main-navigation #primary-menu-wrap {
    top: 0;
    width: unset;
  }
}
